export class UIBootstrap5 {
    app;
    ui;
    static install(app) {
        app.$ui.bootstrap = new this(app, app.$ui);
        app.$ui.theme = app.$ui.bootstrap;
    }
    constructor(app, ui) {
        this.app = app;
        this.ui = ui;
        //
    }
    get $helper() {
        return this.app.inject('$helper');
    }
    get $loader() {
        return this.app.inject('$loader');
    }
    renderMessage(messages, type = 'info') {
        if (!Array.isArray(messages)) {
            messages = [messages];
        }
        let text = '';
        messages.forEach((msg) => {
            text += `<div class="">${msg}</div>`;
        });
        const html = this.$helper.html(`<div class="alert alert-${type} alert-dismissible fade show" role="alert">
  ${text}
  <button type="button" class="btn-close" data-bs-dismiss="alert" aria-label="Close"></button>
</div>`);
        const container = this.$helper.selectOne('.c-messages-container');
        if (container) {
            container.appendChild(html);
        }
    }
    clearMessages() {
        const container = this.$helper.selectOne('.c-messages-container');
        if (container) {
            container.innerHTML = '';
        }
    }
    /**
     * @see https://getbootstrap.com/docs/5.0/components/tooltips/#example-enable-tooltips-everywhere
     */
    tooltip(selector = '[data-bs-toggle="tooltip"]', config = {}) {
        return this.$helper.module(selector, 'bs.tooltip', (ele) => this.getOrCreateInstance(bootstrap.Tooltip, ele, config));
    }
    /**
     * @param {string|Element} selector
     * @param {object} config
     */
    modal(selector, config = {}) {
        return this.$helper.module(selector, 'bs.modal', (ele) => this.getOrCreateInstance(bootstrap.Modal, ele, config));
    }
    collapse(selector = '[data-bs-toggle=collapse]', config = {}) {
        return this.$helper.module(selector, 'bs.collapse', (ele) => this.getOrCreateInstance(bootstrap.Collapse, ele, config));
    }
    offcanvas(selector = '[data-bs-toggle="offcanvas"]', config = {}) {
        return this.$helper.module(selector, 'bs.offcanvas', (ele) => this.getOrCreateInstance(bootstrap.Offcanvas, ele, config));
    }
    popover(selector = '[data-bs-toggle="popover"]', config = {}) {
        return this.$helper.module(selector, 'bs.popover', (ele) => this.getOrCreateInstance(bootstrap.Popover, ele, config));
    }
    scrollspy(selector = '[data-bs-spy="scroll"]', config = {}) {
        return this.$helper.module(selector, 'bs.scrollspy', (ele) => this.getOrCreateInstance(bootstrap.ScrollSpy, ele, config));
    }
    tab(selector = '[data-bs-toggle="tab"]', config = {}) {
        return this.$helper.module(selector, 'bs.tab', (ele) => this.getOrCreateInstance(bootstrap.Tab, ele, config));
    }
    toast(selector = '[data-bs-toggle="toast"]', config = {}) {
        return this.$helper.module(selector, 'bs.toast', (ele) => this.getOrCreateInstance(bootstrap.Toast, ele, config));
    }
    keepTab(selector, config = {}) {
        return this.$loader.import('@unicorn/bootstrap/keep-tab.js')
            .then((m) => {
            if (selector) {
                return new m.LoadTab(selector, config);
            }
            return m;
        });
    }
    async buttonRadio(selector, config = {}) {
        let m = await this.$loader.import('@unicorn/bootstrap/button-radio.js');
        if (selector) {
            return m.ButtonRadio.handle(selector, config);
        }
        return m;
    }
    getMajorVersion(module) {
        return Number(module.VERSION.split('.').shift());
    }
    getOrCreateInstance(module, ele, config = {}) {
        if (this.getMajorVersion(module) <= 4) {
            return new module(ele, config);
        }
        else {
            return module.getOrCreateInstance(ele, config);
        }
    }
}
